import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class PhotoService {

  constructor(private http:HttpClient) { }

  getPublicPhotos() {
    return this.http.get('https://flickr-api.azurewebsites.net/photos/public')
  }

  searchPublicPhotos(searchString:string) {
    return this.http.post('https://flickr-api.azurewebsites.net/photos/search', {searchString})
  }
}
